.post {
    display: flex;
    background: #f5f5f5;
    margin-top: 15px;
    box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.5);
    border-radius: 5px;
    padding: 15px;
}

.post__user_logo {
    width: 50px;
}

.post__user_logo img{
    width: 100%;
}

.post_description {
    margin-left: 15px;
}

.post_description__like {
    margin-top: 5px;
}